.description {
  background: #fff;
  padding-top: 40px;

  &__header {
    font-family: Roboto, sans-serif;
    font-size: 10px;
    line-height: 16px;
    text-align: center;
    letter-spacing: 0.67px;
    text-transform: uppercase;
    color: #637381;
    margin-bottom: 20px;
  }

  &__subj-icons {
    display: flex;
    justify-content: space-between;
  }

  &__subj {
    border: 1px solid #007a5c;
    box-sizing: border-box;
    border-radius: 24px;
    font-family: Roboto, sans-serif;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.15px;
    color: #007a5c;
    padding: 8px 15px;
  }

  &__headers {
    margin-top: 90px;
    display: flex;
    justify-content: space-evenly;
  }

  &__headers span {
    font-family: Roboto, sans-serif;
    font-size: 32px;
    line-height: 44px;
    text-align: center;
    letter-spacing: 0.2px;
    color: #023135;
  }

  &__smartphone-block {
    display: flex;
    justify-content: center;
    padding-top: 84px;
    position: relative;
  }

  &__better {
    font-family: Roboto, sans-serif;
    font-size: 24px;
    line-height: 32px;
    text-align: center;
    letter-spacing: 0.15px;
    color: #023135;
    margin-top: 25px;
    margin-bottom: 40px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &__item h4 {
    font-family: Roboto, sans-serif;
    font-size: 20px;
    line-height: 28px;
    text-align: center;
    letter-spacing: 0.15px;
    color: #023135;
    margin-top: 20px;
  }

  &__item p {
    font-family: Roboto, sans-serif;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    letter-spacing: 0.15px;
    color: #000;
  }

  @media screen and (max-width: 768px) {

    &__headers {
      margin-top: 10px;
      flex-direction: column;
    }

    &__headers span {
      font-size: 25px;
      line-height: 50px;
      text-transform: uppercase;
    }

    &__subj-icons {
      display: none;
    }

    &__header {
      font-size: 27px;
    }
  }

  @media screen and (max-width: 414px) {
    &__phone {
      display: none;
    }
        
    &__smartphone-block {
      flex-direction: column;
      padding-top: 45px;
    }
        
    &__header {
      line-height: 35px;
      font-size: 20px;
    }

    &__headers span {
      font-size: 20px;
      line-height: 38px;
    }

    &__text {
      font-size: 20px;
    }

    &__item {
      width: 100%;
    }
  }
}
