.modal {
  &__header {
    font-family: Roboto, sans-serif;
    font-size: 32px;
    line-height: 44px;
    text-align: center;
    letter-spacing: 0.2px;
    color: #007a5c;
  }
}

.form__input {
  margin-bottom: 20px !important;
  padding: 25px 38px !important;
}

.form__button {
  width: 100%;
  background: #007a5c;
  padding: 18px 38px;
  border: none;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  letter-spacing: 0.15px;
  color: #fff;
  transition: 0.5s;
}

.form__button:hover {
  box-shadow: 10px 20px 25px rgba(76, 106, 170, 0.22);
}
